/* All colors */

:root {
  --pink: #ffa7c4;
  --dark-pink: #d23669;
  --light-gray: #f7f7f7;
  --slightly-dark-gray: #c9c9c9;
  --quite-dark-gray: #34363b;
  --very-dark-gray: #282c35;
  --super-dark-gray: #242730;
  --dark-gray: #373c49;
  --almost-black: #222;
  --white-slightly-transparent: hsla(0, 0%, 100%, 0.88);
  --white-really-transparent: hsla(0, 0%, 100%, 0.2);
  --white: #fff;
  --white-dirty: #e6e6e6;
  --yellow-white: rgb(255, 253, 225);
}

/* Dark theme */

body.dark {
  --bg-lighter: var(--very-dark-gray);
  --bg-darker: var(--super-dark-gray);
  --textNormal: var(--white-slightly-transparent);
  --textTitle: var(--white);
  --textLink: var(--pink);
  --sidebar-bg: var(--dark-gray);
  --sidebar-text: var(--white-dirty);
  --sidebar-border-color: black;
  --skewed-bg-color: var(--quite-dark-gray);
}

/* Light theme */

body.light {
  --bg-lighter: white;
  --bg-darker: var(--white-really-transparent);
  --textNormal: var(--almost-black);
  --textTitle: var(--almost-black);
  --textLink: var(--dark-pink);
  --sidebar-bg: var(--yellow-white);
  --sidebar-text: var(--almost-black);
  --sidebar-border-color: var(--slightly-dark-gray);
  --skewed-bg-color: var(--light-gray);
}

/* CSS reset */

* {
  margin: 0;
  padding: 0;
}

/* The document body */

body {
  color: var(--textNormal);
  transition: color 0.2s ease-out 0s, background 0.2s ease-out 0s;
  transition-property: color, background;
  transition-duration: 0.2s, 0.2s;
  transition-timing-function: ease-out, ease-out;
  transition-delay: 0s, 0s;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  background: -webkit-linear-gradient(right, var(--bg-darker), var(--bg-lighter));
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* I like nice code snippets */

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
/* The left sidebar with the avatar image */

.sidenav {
  height: 100%;
  width: 20em;
  position: fixed;
  border-right: 1px solid var(--sidebar-border-color);
  top: 0;
  left: 0;
  background: var(--sidebar-bg);
  overflow-x: hidden;
  padding: 1em;
  padding-top: 2em;
  color: var(--sidebar-text);
  text-align: center;
}

/* The main content wrapper */

.main {
  margin-top: -395px;
  margin-left: 22em;
  padding: 2em;
}

/* The cards */

.cards {
  padding: 1.5em;
  margin: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
  justify-content: space-around;
}

/* The sections icons */

.cards-icon {
  margin: .15em;
  font-size: 6em;
}

/* The sections icon descriptions */

.card-description {
  max-width: 240px;
}

/* The Get in touch form wrapper */

.get-in-touch-form {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

/* The oblique background */

.skewed-bg {
  position: relative;
  background: var(--skewed-bg-color);
  padding: 300px 0;
  -webkit-transform: skew(0deg, -10deg);
  transform: skew(0deg, -10deg);
  margin-top: -200px;
  z-index: -1;
  /* The color change transition when changing the theme, identical to the body transition */
  transition: color 0.2s ease-out 0s, background 0.2s ease-out 0s;
  transition-property: color, background;
  transition-duration: 0.2s, 0.2s;
  transition-timing-function: ease-out, ease-out;
  transition-delay: 0s, 0s;
}

/* Media rules */

@media only screen and (max-width: 1026px) {
  /* Hide the sidebar and remove the main content left margin */
  .sidenav {
    visibility: hidden;
    opacity: 0;
    transform: translate(-20em, 0);
    -webkit-transform: translate(-20em, 0);
    -moz-transform: translate(-20em, 0);
    -o-transform: translate(-20em, 0);
    -ms-transform: translate(-20em, 0);
    transition: all 600ms;
  }
  .main {
    margin-left: 0;
  }
}

@media only screen and (min-width: 1026px) {
  /* Hide the sidebar and remove the main content left margin */
  .sidenav {
    visibility: visible;
    opacity: 1;
    transition: opacity 600ms, visibility 600ms;
  }
}
/* Theme switch button */

label {
    color: black;
    color: var(--text, black);
}

.theme-switch-input:checked~label {
    --text: white;
}

.theme-switch-input, .theme-switch-label {
    bottom: 1em;
    left: 3em;
    position: absolute;
    z-index: 1;
}

.theme-switch-input {
    opacity: 0;
}

.theme-switch-input:hover+.theme-switch-label, .theme-switch-input:focus+.theme-switch-label {
    background-color: lightSlateGray;
}

.theme-switch-input:hover+.theme-switch-label span::after, .theme-switch-input:focus+.theme-switch-label span::after {
    background-color: #d4ebf2;
}

.theme-switch-label {
    margin: 60px;
    transition: background-color 200ms ease-in-out;
    width: 120px;
    height: 50px;
    border-radius: 50px;
    text-align: center;
    background-color: slateGray;
    box-shadow: -4px 4px 15px inset rgba(0, 0, 0, 0.4);
}

.theme-switch-label::before, .theme-switch-label::after {
    font-size: 2rem;
    position: absolute;
    -webkit-transform: translate3d(0, -50%, 0);
            transform: translate3d(0, -50%, 0);
    top: 50%;
}

.theme-switch-label::before {
    content: '\263C';
    right: 100%;
    margin-right: 10px;
    color: orange;
}

.theme-switch-label::after {
    content: '\263E';
    left: 100%;
    margin-left: 10px;
    color: lightSlateGray;
}

.theme-switch-label span {
    position: absolute;
    bottom: calc(100% + 10px);
    left: 0;
    width: 100%;
}

.theme-switch-label span::after {
    position: absolute;
    top: calc(100% + 15px);
    left: 5px;
    width: 40px;
    height: 40px;
    content: '';
    border-radius: 50%;
    background-color: lightBlue;
    transition: background-color 200ms, -webkit-transform 200ms;
    transition: transform 200ms, background-color 200ms;
    transition: transform 200ms, background-color 200ms, -webkit-transform 200ms;
    box-shadow: -3px 3px 8px rgba(0, 0, 0, 0.4);
}

.theme-switch-input:checked~.theme-switch-label {
    background-color: lightSlateGray;
}

.theme-switch-input:checked~.theme-switch-label::before {
    color: lightSlateGray;
}

.theme-switch-input:checked~.theme-switch-label::after {
    color: turquoise;
}

.theme-switch-input:checked~.theme-switch-label span::after {
    -webkit-transform: translate3d(70px, 0, 0);
            transform: translate3d(70px, 0, 0);
}
/* The navbar */

.navbar {
    font-size: 1.2em;
}

.navbar li {
    display: inline;
    text-align: center;
}

.navbar li span {
    display: inline-block;
    padding: .75rem 0;
    margin: 0;
    color: var(--textLink);
    text-decoration: none;
}

.navbar hr {
    height: .07rem;
    margin: 0;
    background-color: var(--textLink);
    border: none;
    transition: .3s ease-in-out;
}

/* The main content */

.content {
    padding-top: 1em;
    text-align: center;
    height: 100%;
}

/* Media rules */

@media only screen and (max-width: 1026px) {
    /* Make the navbar links stack one over the other */
    .navbar li {
        display: block;
    }
}
/* The avatar image */

.image-with-caption {
    border-radius: 50%;
}
.language-bar {
    width: 100%;
    padding-bottom: 1.8em;
}

.language-bar li {
    display: inline;
    text-align: center;
}

.language-bar li img {
    display: inline-block;
    padding: 1em;
}

.language-bar li img:hover {
    opacity: .8;
}

.language-bar li .active {
    opacity: 1;
}

.language-bar li .inactive {
    opacity: .7;
}


.form-wrapper {
    margin: 1em;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    height: 100%;
}

input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
    color: var(--textNormal);
}

input::-ms-input-placeholder, textarea::-ms-input-placeholder {
    color: var(--textNormal);
}

input::placeholder, textarea::placeholder {
    color: var(--textNormal);
}

input:hover, textarea:hover, #submit:hover {
    background: var(--sidebar-bg);
    color: var(--textNormal);
}

textarea, input, #submit {
    padding: 1em;
    width: 100%;
    border: solid 1px var(--textNormal);
    background: transparent;
    color: var(--textNormal);
    font-size: 1em;
    outline: none;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
}

textarea {
    height: 110px;
    max-height: 110px;
}

input {
    height: 50px;
    border-bottom: none;
}

#submit {
    margin: -4px 0px 0px 0px;
    cursor: pointer;
    border-top: none;
}
